@import url('https://fonts.googleapis.com/css?family=Lato');


a {
  text-decoration: none;
}

.article-image {
  width: 100%;
  height: auto;
}

#contact-panel .page-content {
  background-color: #fff;
}

#contact-panel .mdl-cell {
  background-color: #fff;
}

#contact-panel .mdl-card {
  margin: 0 auto;
}

#contact-panel .mdl-site-copy {
  background-color: #fff;
}

#contact-panel p {
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  text-align: center;
}

#contact-button {
  color: #fff;
  position: fixed;
  display: block;
  right: 0;
  bottom: 0;
  margin-right: 40px;
  margin-bottom: 40px;
  z-index: 900;
  cursor: pointer;
  /* for iOS touch event issue */
}

.form-contact .mdl-textfield {
  width: 100%;
}

h2.page-intro {
  font-family: "Lato", sans-serif;
  font-size: 18px;
  color: #4e4b4b;
  padding: 0;
}

.hidden {
  display: none !important;
  opacity: 1 !important;
}

img {
  max-width: 100%;
  max-height: 100%;
}

#logo {
  position: absolute;
  left: 0;
  font-size: 18px;
  color: #048679;
}

.mdl-card__actions {
  margin-top: auto;
}



@media screen and (max-width: 768px) {
  .page-intro {
    font-size: 16px!important;
  }
  .page-title {
    font-size: 36px!important;
  }
}

@media screen and (max-width: 479px) {
  .navigation-tabs {
    justify-content: center!important;
  }
  #splash-pg-left {
    margin-top: -10%;
  }
  #splash-pg-left, #splash-pg-right, #splash-pg-left img {
    max-height: 45vh;
  }
}

.mdl-tabs__tab-bar {
  border-bottom: 1px solid #efefef;
}
.navigation-tabs {
  width: 100vw;
  background-color: white;
  z-index: 3;
  position: -webkit-sticky;
  position: sticky;
  top: -1px;
  font-family: 'Lato', open-sans;
  justify-content: flex-end;
}

.page-intro {
  font-weight: 400;
  line-height: normal;
  margin: 0;
}

.page-intro.page-title {
  font-family: "Lato", sans-serif;
  font-size: 48px;
  color: #2d2b2b;
}

.portfolio-card .mdl-card__title {
  padding-bottom: 0;
}

#splash-pg-left, #splash-pg-right {
  margin: auto;
}

#splash-pg-left {
  img {
    width: auto;
  }
}

#splash-pg-right div {
  text-align: center;
}

.site-content {
  margin: 14px auto;
  min-height: calc(100vh - 77px); /* force content to span full height of pg */
}

.site-max-width {
  margin: auto;
}

.site-copy {
  max-width: 700px;
}

.social-media-icons {
  padding: 1em;
}

.social-media-icons a {
  display: inline-block;
  margin: 5px;
}

.social-media-icon {
  max-width: 1.6em;
  transition: .1s transform;
}

.social-media-icon:hover {
  transform: scale(1.2);
}

.welcome-card {
  align-items: center;
}

#work-panel .portfolio-card {
  background-color: #fff;
}

/* Utility Classes */

.fh-minus-nav {
  min-height: calc(100vh - 88px);
}

.p1 {
  padding: 1em;
}
.p2 {
  padding: 2em;
}
.pt1 {
  padding-top: 1em;
}
.pl1 {
  padding-left: 1em;
}
.pr1 {
  padding-right: 1em;
}
.pb1 {
  padding-bottom: 1em;
}

.font-sm {
  font-size: 14px!important;
}


